.wp-block-blockylicious-curvy {
	position: relative;

	background-color: #ec4899;
	padding: 80px 50px;

	.curve{
		position: absolute;
		left:0;
		width: 100%;
		overflow: hidden;

		&.top-curve{
			top: 0;
		}

		&.bottom-curve{
			top: 0;
		}

		svg{
			position: absolute;
			left: 0;
			top: 0;
		}
	}
}
